
  @import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans&family=Poppins:wght@400;600&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

html{
  font-size: 62.5%;
}
body{
  background-color: rgb(61, 58, 58);
}
h1{
  font-size: 3.2 rem;
  margin: 2rem 0;
  text-align: center;
  color: white;
}
h2{
  font-size: 2.4rem;
  font-family: 'IBM Plex Sans', sans-serif;
}

p{
  margin: 2rem 0;
  color: rgb(21, 19, 60, 0.5);
  font-weight: 400;
  font-size: 1.6rem;
  display: inline-block;
}
span{
  text-transform: capitalize;
  font-weight: bold;
}
.stories-div{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 4rem;
}
.card{
  min-width: 20rem;
  width: 40vw;
  padding: 4rem;
  background-color: #ffffff;
  box-shadow: rgb(100, 100, 111, 0.2) 0px 7px 29px 0px ;
  border-radius: 0.5rem;
  color: #15133c;
} 
.card-button{
  flex-direction: row;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.card-button a{
  color: #92b4ec;
  font-weight: 400;
  font-size: 1.6rem;
}

.card-button a:last-child {
  color: red;
}
form{
  display: grid;
  place-items: center;
}

input{
  padding: 1rem;
  min-width: 40rem;
  font-size: 2rem;
  text-transform: capitalize;
  border: none;
  border-bottom: 0.2rem solid #15133c;
  outline: none;
}
.pagination-btn {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.pagination-display{
  color: white;
}

button{
  padding: 0.8rem 2.2rem;
  border: none;
  font-size:1.5rem;
  background-color: #15133c;
  color: #ffffff;
  margin: 3rem;
  cursor: pointer;
  display: inline-block;
}

@media (max-width:998px){
  html{
    font-size:56%;
  }
  .card{
    min-width: 45rem;
    padding: 2.5rem;
  }
}

@media (max-width:998px){
  html{
    font-size:51%;
  }
  .card{
    min-width: 35rem;
    padding: 2rem;
  }
}
